import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import { useState } from 'react';
import * as React from 'react';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import LibraryBooksIcon from '@mui/icons-material/LibraryBooks';
import LocalMoviesIcon from '@mui/icons-material/LocalMovies';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import Fade from '@mui/material/Fade';
import Fab from '@mui/material/Fab';
import PropTypes from 'prop-types';
import {Link, Route, Routes} from "react-router-dom";
import Home from "../Home/Home";
import {useEffect} from "react";
import Libro from "../Libros/Libro";
import Shop from '../Shop/Shop';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';

const ScrollTop = (props) => {
    const {children, window} = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
        target: window ? window() : undefined,
        disableHysteresis: false,
        threshold: 50,
    });

    const handleClick = (event) => {

        const anchor = (event.target.ownerDocument || document).querySelector('#navmenu',);
    
        if (anchor) {
          anchor.scrollIntoView({
            behavior: "smooth",
          });
        }
    };
    return (
        <Fade in={trigger}>
            <Box
                onClick={handleClick}
                role="presentation"
                sx={{ position: 'fixed', bottom: 16, right: 16 }}
            >
                {children}
            </Box>
        </Fade>
    );
}

const ElevationScroll = (props) => {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
        disableHysteresis: true,
        threshold: 0,
        target: window ? window() : undefined,
    });

    return React.cloneElement(children, {
        elevation: trigger ? 4 : 0,
    });
}

ScrollTop.propTypes = {
  children: PropTypes.element.isRequired,
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default function Nav(props) {
    
    const [search, setsearch] = useState(false);
    const [texturl, settexturl] = useState('');
    const [tituloNav, settituloNav] = useState('');
    const [state, setState] = useState({
      left: false
    });

    const toggleDrawer = (anchor, open) => (event) => {
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
  
      setState({ ...state, [anchor]: open });
    };

    const ruta = (text) => {
      debugger;
      if(text === 'Home'){
        return '/'
      }
      else if(text === 'libro'){
        return '/libro'
      }
      else if(text === 'Peliculas'){
        return '/Peliculas'
      }
    };

    const list = (anchor) => (
      <Box
        sx={{ width:250 }}
        role="presentation"
        onClick={toggleDrawer(anchor, false)}
        onKeyDown={toggleDrawer(anchor, false)}
      >
        <List>
          <ListItem key='Home'disablePadding>            
            <Link to="/" style={{textDecoration:"none", color:"black", width:'100%'}} onClick={ () => settituloNav('Curriculum Vitae') } >                
            <ListItemButton>
                  <ListItemIcon>
                    <AccountBoxIcon /> 
                  </ListItemIcon>
                  <ListItemText primary='Curriculum Vitae' />
                </ListItemButton>
            </Link>
          </ListItem>
        <Divider />
          <ListItem key='Libreria'disablePadding>
            <Link to="/Libros" style={{textDecoration:"none", color:"black", width:'100%'}} onClick={ () => settituloNav('Libreria') }>              
            <ListItemButton>
                  <ListItemIcon>
                        <LibraryBooksIcon /> 
                  </ListItemIcon>
                  <ListItemText primary='Libreria' />
                </ListItemButton>  
            </Link>
          </ListItem>
        <Divider />
          <ListItem key='Shop'disablePadding>              
            <Link to="/Shop" style={{textDecoration:"none", color:"black", width:'100%'}} onClick={ () => settituloNav('Shop') }>    
                <ListItemButton>
                  <ListItemIcon>
                        <ShoppingBasketIcon /> 
                  </ListItemIcon>
                  <ListItemText primary='Shop' />
                </ListItemButton>
              </Link>
          </ListItem>
        <Divider />
        </List>
      </Box>
    );
    const validaurl = (url) => {
      if(url === '/'){
        settituloNav('Curriculum Vitae');
      }
      else if(url === '/Libros'){
        settituloNav('Libreria');
      }
      else if(url === '/Shop'){
        settituloNav('Shop');
      }
    }

        
  useEffect( ()=>{
      validaurl(window.location.pathname);
  },[] )

	return (
    <>
        <React.Fragment>
        <Box sx={{ flexGrow: 1 }} id="navmenu">
            <ElevationScroll {...props}>
                <AppBar>
                    <Toolbar>
                    <IconButton
                    size="large"
                    edge="start"
                    color="inherit"
                    aria-label="open drawer"
                    sx={{ mr: 2 }}
                    onClick={toggleDrawer('left', true)}
                    >
                    <MenuIcon />
                    </IconButton>
                    <Drawer
                    anchor={'left'}
                    open={state['left']}
                    onClose={toggleDrawer('left', false)}
                    >
                    {list('left')}
                    </Drawer>
                    <Typography
                        variant="h6"
                        noWrap
                        component="div"
                        sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
                    >
                        {tituloNav}
                    </Typography>
                </Toolbar>
                </AppBar>
            </ElevationScroll>
            <ScrollTop {...props}>
                <Fab size="small" aria-label="scroll back to top">
                    <KeyboardArrowUpIcon />
                </Fab>
            </ScrollTop>
        </Box>
        </React.Fragment>        
        <div>
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="curriculumvitae" element={<Home />} />
                <Route path="Libros" element={<Libro />} />
                <Route path="Shop" element={<Shop />} />
                {/* <Route path="libro" element={<Perfil title={nombreuser} name={user} />} /> curriculumvitae
                <Route path="Pelicula" element={<Configuracion />} />  */}
            </Routes>
        </div>
    </>
	);
}
