import { useState, useRef, useEffect } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import UndoIcon from '@mui/icons-material/Undo';

export default function Infolibro(props) {

    const backhomelibro = () => {
        props.estbacklibro(false);
    };
    useEffect(() => {
        debugger;
        console.log(props);
    }, [props]);

    return (
        <div style={{ paddingLeft:'1%', paddingRight:'1%' }}>
            <br /><br />
            <Typography gutterBottom variant="h3" component="div">
                {props.infolibro[1]}
                <UndoIcon sx={{ cursor:'pointer', paddingLeft:'1%' }} onClick={backhomelibro}/>
            </Typography>            
            <Card sx={{ with: 500  }}>
                <CardMedia
                    sx={{ width:'12%' }}    
                    component="img"
                    height="100%"                    
                    width="15rem"
                    image={props.infolibro[0]}
                    alt="green iguana"
                />
                <Typography gutterBottom variant="h6" component="div">
                   Fecha de publicacin: {props.infolibro[3]}
                </Typography>
                <Typography gutterBottom variant="h6" component="div">
                   Autor: {props.infolibro[4]}
                </Typography>
                <Typography gutterBottom variant="h6" component="div">
                    {props.infolibro[2]}
                </Typography>
            </Card>
        </div>
    )
}