import { useState, useRef, useEffect } from 'react';
import { Box } from "@mui/material";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import Masonry from '@mui/lab/Masonry';
import Pagination from '@mui/material/Pagination';
import Tabs from '@mui/material/Tabs';
import SearchIcon from '@mui/icons-material/Search';
import { styled, alpha } from '@mui/material/styles';
import InputBase from '@mui/material/InputBase';
import Infolibro from './Infolibro';

const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    margin: '1%',    
    border: '#1976D2',
    borderStyle: 'solid',
    width: '20%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
    },
}));
  
const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
}));
  
const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'inherit',
    width: '100%',
    '& .MuiInputBase-input': {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)})`,
      transition: theme.transitions.create('width'),
      width: '100%',
      [theme.breakpoints.up('sm')]: {
        width: '30ch',
        '&:focus': {
          width: '40ch',
        },
      },
    },
}));

export default function Libro(props) {
    
    const [libros, setlibros] = useState([]);
    const [page, setPage] = useState(1);
    const [namebook, setnamebook] = useState('');
    const [search, setsearch] = useState(false);
    const [sellibro, setsellibro] = useState(false);
    const [infolibro, setinfolibro] = useState([]);
  
    const getBooks = async () => {
      
      await fetch(
        `https://www.googleapis.com/books/v1/volumes?q=${namebook}&maxResults=14&startIndex=${page}&key=AIzaSyDLXYdi5BS5bLe3O8DVGeWtTCp1eaCljvs`
      )
        .then((res) => res.json())
        .then((data) => {
          setlibros(data.items); 
          setsearch(true);  
        })
        .catch((err) => {
          console.error(err);
          setsearch(false);  
        });
    };

    function pulsar(e) {
        if (e.key === 'Enter') {
            getBooks();
        }
    };

    const libroinfo = (img, title, descrip) => {
        debugger;
        if(descrip === undefined){
            descrip = 'Sin descripcin';
        }
        else if(title === undefined){
            setsellibro(true);
        }
        //setinfolibro([img, title, descrip]);
    }

    return (
        <>

      <Box sx={{ width: '100%', minHeight: 377, paddingTop: '3%' }}>   
      { sellibro ? 
        <Infolibro infolibro={infolibro} estbacklibro={ (value) => setsellibro(value) } />
        :
        <>
            <div>
            <Tabs centered>
            <Search>
                    <SearchIconWrapper>
                        <SearchIcon />
                    </SearchIconWrapper>
                    <StyledInputBase
                        placeholder="Search"
                        inputProps={{ 'aria-label': 'search' }}
                        onChange={(e) => setnamebook(e.target.value) }
                        onKeyPress={(e) => pulsar(e)}
                    />
            </Search>
            </Tabs>
            </div>     
            <Box sx={{ width: '100%', minHeight: 377 }}>
            <Masonry columns={7} spacing={2}>
            {
            libros && libros.map((books) => (
                <>
                <CardActionArea onClick={ () => {
                    setsellibro(true);
                    setinfolibro([books.volumeInfo.imageLinks !== undefined
                        ? books.volumeInfo.imageLinks.thumbnail
                        : 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/6c/No_image_3x4.svg/1280px-No_image_3x4.svg.png', 
                        books.volumeInfo.title, books.volumeInfo.description, books.volumeInfo.publishedDate,  books.volumeInfo.authors]);
                    }} >
                    <Card sx={{ maxWidth: 345, minHeight: 300  }}>
                        <CardMedia
                            component="img"
                            height="100%"
                            image={
                            books.volumeInfo.imageLinks !== undefined
                                ? books.volumeInfo.imageLinks.thumbnail
                                : 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/6c/No_image_3x4.svg/1280px-No_image_3x4.svg.png'
                            }
                            alt="green iguana"
                        />
                        <CardContent>
                            <Typography gutterBottom variant="h5" component="div">
                            {books.volumeInfo.title}
                            </Typography>
                        </CardContent>
                    </Card>
                </CardActionArea>
                </>
            ))
            }
            </Masonry>
            {
            search ?             
                <Tabs centered>
                    <Pagination count={14} page={page} onClick={(e) => {setPage(parseInt(e.target.innerText)); getBooks() }} />
                </Tabs>
                :
                <></>
            }
            </Box>
        </>
      }
      </Box>
        </>
    )
}